<!--首页-->
<template>
  <div id="index">
    <div>
      <!--轮播-->
      <el-carousel indicator-position="outside" id="banner-wrapper">
        <el-carousel-item v-for="(item, index) in advertisementList" :key="index" >
          <div  @click="jumpTo(item)" :style="{'background':`url(${item.imageUrl}) no-repeat center`}"  style="min-width: 1200px;height: 360px"  ></div>
        </el-carousel-item>
      </el-carousel>
      <!--当季精选-->
      <div id="seasonal-commodity-wrapper" v-if="seasonalCommodityList.length > 0 && currentCommodityList && currentCommodityList.length > 0">
        <img src="@/static/images/sale-hot.png">
        <div class="seasonal-commodity-list">
          <div
            v-for="(item, index) in currentCommodityList"
            :key="index"
            class="commodity"
            @click="$router.push({name: 'Detail', params: {commodityCode: item.commodityCode}})"
          >
            <div style="position: relative;">
              <img :src="item.imageUrl">
              <template v-if="item.presell">
                <i class="iconfont icon-yushou"></i>
                <div class="trangle"></div>
              </template>
              <div class="commodity-name">{{ item.commodityName }}</div>
              <div class="commodity-price">&yen; {{ item.commoditySalesPrice.toFixed(2) }}</div>
            </div>
            <div class="add-cart" @click.stop="addCart($event, item)">加入购物车</div>
          </div>
          <a href="javascript:;" class="scan-button" @click="selectNextList">
            <i class="iconfont icon-few"></i>
          </a>
        </div>
      </div>
      <!--商品专区-->
      <div v-if="promoteposList.length > 0" id="promotepos-list">
        <div v-for="(item, index) in promoteposList" :key="index" class="promotepo-wrapper">
          <div class="promotepo-title">
            <span class="main-title">{{ item.categoryName }}</span>
            <span class="sub-head">/ {{ item.advert }}</span>
          </div>
          <div class="promotepo-content" style="position: relative;">
            <template v-if="item.presell">
              <i class="iconfont icon-yushou"></i>
              <div class="trangle"></div>
            </template>
            <img :src="item.imageUrl">
            <div class="commodity-list">
              <div
                v-for="(unit, index) in item.commodityList"
                :key="index"
                class="commodity-wrapper"
                @click="$router.push({name: 'Detail', params: {commodityCode: unit.commodityCode}})"
              >
                <img :src="unit.imageUrl + andImageMiddleWidth">
                <div class="commodity-name">{{ unit.commodityName }}</div>
                <div class="commodity-price">&yen; {{ unit.commoditySalesPrice.toFixed(2) }}</div>
                <div class="add-cart" @click.stop="addCart($event, unit)">加入购物车</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Flyball ref="flyball" />
    </div>

    <Bar  :domArr="domArr" v-if="domArr.length > 0" />
  </div>
</template>
<script>
import Flyball from '@/components/Flyball.vue'
import Bar from '@/components/Bar.vue'

export default {
  name: 'Index',
  components: {
    Flyball,
    Bar
  },
  data () {
    return {
      advertisementList: [],
      seasonalCommodityList: [],
      currentCommodityIndex: null,
      promoteposList: [],
      currentCommodityList: [],
      domArr:[]

    }
  },
  computed: {
    loginStatus () {
      return this.$store.state.loginStatus
    }
  },
  created () {
    this.getHomePageInfo()
    this.getHomePageCommodity()
    this.getPromoteposList()


  },
  methods: {
    getDomY(){
      //获取节点
      this.$nextTick(()=>{
        this.domArr=document.querySelectorAll(".promotepo-wrapper");
      })
    },
    jumpTo (item) {
      switch (item.slideType) {
        // 跳转到商品详情页
        case 1:
          this.$router.push({ name: 'Detail', params:{ commodityCode: item.slideValue }})
          break
        // 跳转到指定URL
        case 2:
          location.href = item.slideValue
          break
        // 跳转到专题页面
        case 3:
          // location.href = item.slideValue
          break
      }
    },
    // 获取首页商品轮播和商品列表
    getHomePageInfo () {
      // let advertisementList = localStorage.getItem('advertisementList')
      // if (advertisementList) return this.advertisementList = JSON.parse(advertisementList)
      this.axios.get(this.api.getHomePageInfo).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.advertisementList = res.data.result.advertisementList
          // localStorage.setItem('advertisementList', JSON.stringify(res.data.result.advertisementList))
          localStorage.setItem('guessYouLikeCommodityList', JSON.stringify(res.data.result.guessYouLikeCommodityList))
        }
      })
    },
    // 获取当季精选和商品专区
    getHomePageCommodity () {
      this.axios.get(this.api.getHomePageCommodity).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.seasonalCommodityList = res.data.result.seasonalCommodityList
          this.selectNextList()
          localStorage.setItem('guessYouLikeCommodityList', JSON.stringify(res.data.result.guessYouLikeCommodityList))
        }
      })
    },
    // 获取商品专区
    getPromoteposList () {
      this.axios.get(this.api.getPromoteposList).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.promoteposList = res.data.result
          this.getDomY()
        }
      })
    },
    // 选择下一个当季精选商品列表
    selectNextList () {
      // 如果商品列表的数量小于6，则返回列表
      if (this.seasonalCommodityList.length <= 6) return this.currentCommodityList = this.seasonalCommodityList
      let list = this.seasonalCommodityList.slice(this.currentCommodityIndex, this.currentCommodityIndex + 6)
      // 当季精选商品列表首尾相连
      if (list.length === 6) {
        this.currentCommodityList = list
        this.currentCommodityIndex += 6
      } else {
        let index = 6 - list.length
        let nextList = this.seasonalCommodityList.slice(0, index)
        this.currentCommodityList = list.concat(nextList)
        this.currentCommodityIndex = index
      }
    },
    // 加入购物车
    addCart (event, item) {
      if (this.loginStatus) {
        let param = {
          commodityCode: item.commodityCode,
          num: 1,
          secKill: false
        }
        this.axios.post(this.api.save, param).then(res => {
          if (res.status === 200 && res.data.code === 200) {
            // 加入购物车动画
            this.$refs.flyball.drop(event)
            setTimeout(() => {
              this.$store.dispatch('getCartNum')
            }, 600)
          } else {
            this.$message({
              message: res.data.remark,
              type: 'warning'
            })
          }
        })
      } else {
        // 如果未登录，则跳转到登录页面
        this.$router.push({ name: 'Login', query: {'redirect': 'Index', commodityCode: item.commodityCode }})
      }
    }
  }
}
</script>
<style lang="stylus">
$width = 1200px

#index
  #banner-wrapper
    margin: 0 auto
    width: 100%
    min-width: 1200px
    .el-carousel
      position: relative
    .el-carousel__container
      height: 360px
      .el-carousel__item
        img
          width: 100%
          cursor: pointer
    .el-carousel__indicators
      position: absolute
      bottom: 0
      left: 50%
      transform: translateX(-50%)
  #seasonal-commodity-wrapper
    margin-top: 20px
    text-align: center
    .seasonal-commodity-list
      position: relative
      margin: 0 auto
      display: flex
      overflow: hidden
      align-item: center
      width: 1100px
      box-shadow: 0 5px 20px 5px #ddd
      -moz-box-shadow: 0 5px 20px 5px #ddd
      -webkit-box-shadow: 0 5px 20px 5px #ddd
      .commodity
        position: relative
        flex-shrink: 0
        width: 170px
        border-right: 1px solid #ddd
        > div
          padding: 10px
          cursor: pointer
        img
          width: 150px
          height: 150px
        .commodity-name
          font-size: 14px
          overflow: hidden
          text-overflow: ellipsis
          white-space: nowrap
        .commodity-price
          margin-top: 5px
          font-size: 14px
          font-weight: 700
          color: #ff4e00
        .add-cart
          position: absolute
          bottom: -50px
          width: 150px
          height: 30px
          line-height: 30px
          font-weight: 700
          color: #333
          background: #ffc000
          cursor: pointer
          transition: bottom 0.3s
          -moz-transition: bottom 0.3s /* Firefox 4 */
          -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
          -o-transition: bottom 0.3s /* Opera */
        &:hover
          .add-cart
            bottom: 0
            transition: bottom 0.3s
            -moz-transition: bottom 0.3s /* Firefox 4 */
            -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
            -o-transition: bottom 0.3s /* Opera */
      .scan-button
        position: absolute
        top: 0
        right: 12px
        display: inline-block
        width: 50px
        line-height: 180px
        text-align: center
        .icon-few
          font-size: 30px
          color: #C3C3C3
  #promotepos-list
    margin: 0 auto
    width: 1200px
    .promotepo-wrapper
      margin-top: 50px
      .promotepo-title
        text-indent: 5rem
        color: #666
        .main-title
          font-size: 22px
        .sub-head
          font-size: 14px
          font-family: '楷体'
      .promotepo-content
        display: flex
        height: 460px
        .commodity-list
          width: 750px
          overflow: hidden
          margin-left: 10px
          .commodity-wrapper
            float: left
            position: relative
            width: 185px
            height: 230px
            text-align: center
            overflow: hidden
            cursor: pointer
            img
              width: 80%
              height: auto
            .commodity-name
              margin-top: 35px
              padding-right: 2rem
              font-size: 14px
              overflow: hidden
              text-overflow: ellipsis
              white-space: nowrap
            .commodity-price
              margin-top: 5px
              font-size: 14px
              font-weight: 700
              color: #ff4e00
            .add-cart
              position: absolute
              bottom: -50px
              width: 100%
              height: 50px
              line-height: 50px
              font-weight: 700
              color: #333
              background: #ffc000
              cursor: pointer
              transition: bottom 0.3s
              -moz-transition: bottom 0.3s /* Firefox 4 */
              -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
              -o-transition: bottom 0.3s /* Opera */
            &:hover
              .add-cart
                bottom: 0
                transition: bottom 0.3s
                -moz-transition: bottom 0.3s /* Firefox 4 */
                -webkit-transition: bottom 0.3s /* Safari 和 Chrome */
                -o-transition: bottom 0.3s /* Opera */
</style>



